import Vue from 'vue'
import Router from 'vue-router'
// 
import Login from './views/Login.vue'
// 
import Register from './views/Register.vue'
// 
import Forget from './views/Forget.vue'
// 
import Section from './views/Section.vue'

// 
import Searchpage from './views/Search.vue'
import Searchhot from './components/search/searchhot.vue'
import Searchlist from './components/search/searchlist.vue'
import Searchcards from './components/search/searchcards.vue'
// 

import Index from './views/Index.vue'
// 
import Search from './components/index/search.vue'
// 
import Swipe from './components/index/swipe.vue'
// 
import Cards from './components/index/cards.vue'

// 
import SectionBar from './components/index/sectionbar.vue'

// 
import Detail from './views/Detail.vue'

// 
import My from './views/My.vue'

import Demo from './views/demo.vue'

// 
import Music from './views/Music.vue'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            // name: 'forget',
            redirect: 'demo'
        },
        // 
        {
            path: '/login',
            // name: 'login',
            component: Login
        },
        // 
        {
            path: '/register',
            // name: 'register',
            component: Register
        },
        // 
        {
            path: '/forget',
            // name: 'register',
            component: Forget
        },
        // 
        {
            path: '/section',
            // name: 'register',
            component: Section
        },
        // 
        {
            path: '/my',
            component: My
        },
        // 
        {
            path: '/searchpage',
            component: Searchpage,
            children: [{
                    path: '',
                    components: {
                        Search: Search,
                        Searchhot: Searchhot,
                    }

                },
                {
                    path: 'searchResult',
                    components: {
                        Search: Search,
                        Searchhot: Searchlist
                    },
                    children: [{
                        path: '',
                        components: {
                            Searchcards
                        }
                    }]

                }
            ]
        },
        {
            // 
            path: '/index',
            // name: 'index',
            component: Index,
            children: [{
                path: '',
                components: {
                    Search: Search,
                    Swipe: Swipe,
                    Cards: Cards,
                    SectionBar: SectionBar
                }
            }]
        },
        {
            path: '/detail/:id',
            component: Detail
        },
        {
            path: '/demo',
            component: Demo
        },
        {
            path: '/music',
            component: Music
        }
    ]
})