import http from './api'

//  
function sectionList() {
    return http.post('/api/cms/category/open/list')
}

// 
function getDinyue() {
    return http.get('/api/cms/category/subscription/list');
}

// 
function Dinyue(ids) {
    var formdata = new FormData();
    formdata.append('categoryIds', `${ids}`)

    return http.post('/api/cms/userCategory/subscription/category', formdata)
}
export {
    sectionList,
    getDinyue,
    Dinyue
}