import Vue from 'vue'
import Vuex from 'vuex'

// service
import {
    getIndexNewsList
} from './services/indexlist'

// http
import {
    Search
} from './services/search'

Vue.use(Vuex)

// 
const history = {
    namespaced: true,
    state: {
        historyList: []
    },
    mutations: {
        changeHistoryList(state, payload) {
            state.historyList = payload.arr;
        }
    }
}

// 
const indexNewsList = {
    namespaced: true,
    state: {
        //  --
        newsListArr: [],
        // 
        total: 0,
        // 
        currentCategoryId: 1
    },
    mutations: {
        // statenewsListArr
        changeNewsList(state, payload) {
            state.newsListArr = payload.newsList;
            state.total = payload.total;
        },
        // 
        addNewsList(state, payload) {
            // 
            state.newsListArr = state.newsListArr.concat(payload.newsList);

        }
    },
    actions: {
        setNewsList(context, payload) {
            // id
            getIndexNewsList(payload.pageNum, payload.pageSize, payload.categoryId).then(res => {
                context.commit('changeNewsList', {
                    newsList: res.data.rows,
                    total: res.data.total
                })
            })
        },
        moreNewsList(context, payload) {
            // id
            getIndexNewsList(payload.pageNum, payload.pageSize, context.state.currentCategoryId).then(res => {
                context.commit('addNewsList', {
                    newsList: res.data.rows
                })
            })
        }
    }
}
// 
const resultList = {
    namespaced: true,
    state: {
        searchList: []
    },
    mutations: {
        changeSearchList(state, payload) {
            state.searchList = payload.result;
        }
    },
    actions: {
        setSearchList(context, params) {
            Search(params.title).then(res => {
                // console.log(res)
                context.commit('changeSearchList', {
                    result: res.data.rows
                })
                // console.log(context.state.searchList)
            })
        }
    }
}
export default new Vuex.Store({
    modules: {
        history,
        indexNewsList,
        resultList
    }
})