// 
<template>
    <div class="forget-box">
        <div class="title"></div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
            <el-form-item label=" " prop="name">
                <el-input v-model="ruleForm.name" clearable ref="name"></el-input>
            </el-form-item>

            <el-form-item label=" " prop="password">
                <el-input v-model="ruleForm.password" show-password ref="password"></el-input>
            </el-form-item>

            <el-form-item label=" " prop="password2">
                <el-input v-model="ruleForm.password2" show-password ref="password2"></el-input>
            </el-form-item>

            <el-form-item label=" " prop="number">
                <el-input v-model="ruleForm.number" clearable ref="number"></el-input>
            </el-form-item>

            <el-form-item label=" " prop="test">
                <el-input v-model="ruleForm.test" class="test-ipt" clearable ref="test"></el-input>
                <button @click="countDown" :class="{disabled:!this.flag}" class="test">{{message}}</button>
            </el-form-item>

            <el-form-item class="login-btn">
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>

        <!--  -->
        <div class="forget-back" @click="forgetBack()">
            <i class="el-icon-back"></i>
        </div>
    </div>
</template>

<script>
import { Forget } from '../services/forget'
import { Code } from '../services/code'
export default {
    data () {
        return {
            message: "",
            time: "60",
            flag: true,

            ruleForm: {
                name: "",
                password: "",
                password2: "",
                number: "",
                test: ""
            },
            rules: {
                name: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    {
                        min: 3,
                        max: 12,
                        message: "3-12",
                        trigger: "blur"
                    }
                ],
                password: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    {
                        min: 5,
                        max: 20,
                        message: " 5  20 ",
                        trigger: "blur"
                    }
                ],
                password2: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    {
                        min: 5,
                        max: 20,
                        message: " 5  20 ",
                        trigger: "blur"
                    }
                ],
                number: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    {
                        min: 11,
                        max: 11,
                        message: "11",
                        trigger: "blur"
                    }
                ],
                test: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    {
                        min: 1,
                        max: 6,
                        message: " 1  6 ",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    methods: {

        submitForm (formName) {
            var that = this;
            // 
            if (this.ruleForm.password != this.ruleForm.password2) {
                this.open4('')
                return false;
            }
            this.$refs[formName].validate(valid => {
                if (valid) {
                    Forget(
                        this.$refs.name.value,
                        this.$refs.password.value,
                        this.$refs.number.value,
                        this.$refs.test.value,
                    ).then(res => {
                        // console.log(res);
                        if (res.data.code == "0") {
                            // console.log("");
                            that.open1();
                            this.$router.push("/login");
                        } else {
                            // console.log(res.data.msg);
                            that.open4(res.data.msg)
                        }
                    });
                    // alert("submit!");
                } else {
                    return false;
                }
            });
        },
        // 
        open1 () {
            this.$message({
                showClose: true,
                message: ""
            });
        },
        // 
        open4 (msg) {
            this.$message({
                showClose: true,
                message: msg,
                type: "error"
            });
        },
        // 
        resetForm (formName) {
            this.$refs[formName].resetFields();
        },

        // 
        countDown () {
            Code(this.$refs.number.value);
            // 
            if (this.flag == false) {
                return;
            }
            this.flag = false;
            this.message = this.time + "s"; //60s59s
            let clock = window.setInterval(() => {
                this.time--;
                this.message = this.time + "s";
                if (this.time < 0) {
                    window.clearInterval(clock);
                    this.message = "";
                    this.time = 60;
                    this.flag = true; //
                }
            }, 1000);
        },
        forgetBack () {
            this.$router.replace("/login");
        }
    }
};
</script>
