import http from './api'

// 
function getIndexNewsList(pageNum, pageSize, categoryId) {
    // 
    const formdata = new FormData();
    formdata.append('pageNum', `${pageNum}`);
    formdata.append('pageSize', `${pageSize}`);
    formdata.append('categoryId', `${categoryId}`);

    // promise
    return http.post('/api/cms/article/open/list', formdata)
    // return http.post('/api/cms/article/open/hot/list', formdata)
}

// 
function getHotList(){
    const formdata = new FormData()
    formdata.append('pageNum',1)
    formdata.append('pageSize',10)
    return http.post('/api/cms/article/open/hot/list',formdata)
}
export {
    getIndexNewsList,
    getHotList
}